.Container {
    max-width: 1440px;
    width: 100%;
    margin: auto;

    .Card {
        border-radius: 8px;
        padding: 32px;
        box-shadow: 40px 60px 50px -47px rgba(72, 85, 106, 0.247378);

        .PersonalInfo {
            display: flex;
            align-items: center;
            margin-bottom: 18px;

            .photo-circle {
                display: block;
                width: 28px;
                height: 28px;
                border: 2px solid transparent;
                border-radius: 50%;

                img {
                    border-radius: 50%;
                    width: 28px;     
                }
            }

            .info {
                position: relative;
                z-index: 2;
                margin-left: 15px;
                line-height: 15px;

                h2 {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 13px;
                    line-height: 13px;
                    color: var(--color-white);
                }

                span {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 11px;
                    line-height: 11px;
                    color: var(--color-white);
                    mix-blend-mode: normal;
                    opacity: 0.5;
                }
            }
        }

        p {
            font-style: normal;
            font-weight: 500;
            font-size: 13px;
            line-height: 18px;
            color: var(--color-light-gray);
            mix-blend-mode: normal;
            opacity: 0.7;
        }

        p.TextImportant {
            position: relative;
            z-index: 2;
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            color: var(--color-white);
            margin-bottom: 16px;
            opacity: 1;
        }
    }

    .Card-3, .Card-5 {
        .PersonalInfo {
            .info {
                h2, span {
                    color: var(--color-dark-grey);
                }
            }
        }

        p, p.TextImportant {
            color: var(--color-dark-grey);
        }
    }

    .Card-1 {
        grid-area: card-1;
        position: relative;

        &:before {
            z-index: 1;
            width: 102px;
            height: 104px;
            position: absolute;
            top: 0;
            right: 80px;
            content: "";
            background: url('images/bg-pattern-quotation.svg');

            @media screen and (max-width: 900px) {
                right: 24px;
            }
        }

        .PersonalInfo .photo-circle {
            border: 2px solid #A775F1;
        }
    }

    .Card-2 {
        grid-area: card-2;
    }

    .Card-3 {
        grid-area: card-3;
    }

    .Card-4 {
        grid-area: card-4;

        .PersonalInfo .photo-circle {
            border: 2px solid #733FC8;
        }
    }

    .Card-5 {
        grid-area: card-5;

        @media screen and (min-width:900px) {
            grid-column: 4;
            grid-row: 1/3;
        }
    }

    .Row {
        margin: 71px 24px;
        max-width: 1100px;
        width: auto;
        display: grid;
        gap: 24px;
        grid-template-areas:
            "card-1"
            "card-2"
            "card-3"
            "card-4"
            "card-5";
        
        @media screen and (min-width: 376px) {
            width: 100%;
            margin: 127px auto;
            grid-template-rows: 282px 266px;
            row-gap: 24px;
            column-gap: 30px;
            grid-template-columns: 255px 255px 255px 255px;
            grid-template-areas:
            "card-1 card-1 card-2 card-5"
            "card-3 card-4 card-4 .";
        }

        @media (max-width:1200px) and (min-width:377px) {
            width: 95%;
            grid-template-rows: auto;
            grid-template-columns: auto auto auto;
            grid-template-areas:
            "card-1 card-1 card-2"
            "card-3 card-4 card-4"
            "card-5 card-5 card-5";
        }
    }
  }