@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{--bg-color: #f2f2f2;--color-moderate-violet: hsl(263, 55%, 52%);--color-very-dark-grayish-blue: hsl(217, 19%, 35%);--color-very-dark-blackish-blue: hsl(219, 29%, 14%);--color-white: hsl(0, 0%, 100%);--color-light-gray: hsl(0, 0%, 81%);--color-light-grayish-blue: hsl(210, 46%, 95%);--color-dark-grey: #48556A;--font-barlow-medium: 'Barlow Semi Condensed', sans-serif}html,body,body div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,figure,footer,header,hgroup,menu,nav,section,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent;font-family:'Barlow Semi Condensed', sans-serif;font-family:var(--font-barlow-medium)}body{background:#f2f2f2;background:var(--bg-color)}body .attribution{display:flex;justify-content:center}

.Container{max-width:1440px;width:100%;margin:auto}.Container .Card{border-radius:8px;padding:32px;box-shadow:40px 60px 50px -47px rgba(72,85,106,0.247378)}.Container .Card .PersonalInfo{display:flex;align-items:center;margin-bottom:18px}.Container .Card .PersonalInfo .photo-circle{display:block;width:28px;height:28px;border:2px solid transparent;border-radius:50%}.Container .Card .PersonalInfo .photo-circle img{border-radius:50%;width:28px}.Container .Card .PersonalInfo .info{position:relative;z-index:2;margin-left:15px;line-height:15px}.Container .Card .PersonalInfo .info h2{font-style:normal;font-weight:500;font-size:13px;line-height:13px;color:var(--color-white)}.Container .Card .PersonalInfo .info span{font-style:normal;font-weight:500;font-size:11px;line-height:11px;color:var(--color-white);mix-blend-mode:normal;opacity:0.5}.Container .Card p{font-style:normal;font-weight:500;font-size:13px;line-height:18px;color:var(--color-light-gray);mix-blend-mode:normal;opacity:0.7}.Container .Card p.TextImportant{position:relative;z-index:2;font-weight:600;font-size:20px;line-height:24px;color:var(--color-white);margin-bottom:16px;opacity:1}.Container .Card-3 .PersonalInfo .info h2,.Container .Card-3 .PersonalInfo .info span,.Container .Card-5 .PersonalInfo .info h2,.Container .Card-5 .PersonalInfo .info span{color:var(--color-dark-grey)}.Container .Card-3 p,.Container .Card-3 p.TextImportant,.Container .Card-5 p,.Container .Card-5 p.TextImportant{color:var(--color-dark-grey)}.Container .Card-1{grid-area:card-1;position:relative}.Container .Card-1:before{z-index:1;width:102px;height:104px;position:absolute;top:0;right:80px;content:"";background:url(/testimonials-grid-section/static/media/bg-pattern-quotation.6b460e30.svg)}@media screen and (max-width: 900px){.Container .Card-1:before{right:24px}}.Container .Card-1 .PersonalInfo .photo-circle{border:2px solid #A775F1}.Container .Card-2{grid-area:card-2}.Container .Card-3{grid-area:card-3}.Container .Card-4{grid-area:card-4}.Container .Card-4 .PersonalInfo .photo-circle{border:2px solid #733FC8}.Container .Card-5{grid-area:card-5}@media screen and (min-width: 900px){.Container .Card-5{grid-column:4;grid-row:1/3}}.Container .Row{margin:71px 24px;max-width:1100px;width:auto;display:grid;grid-gap:24px;gap:24px;grid-template-areas:"card-1" "card-2" "card-3" "card-4" "card-5"}@media screen and (min-width: 376px){.Container .Row{width:100%;margin:127px auto;grid-template-rows:282px 266px;grid-row-gap:24px;row-gap:24px;grid-column-gap:30px;-webkit-column-gap:30px;column-gap:30px;grid-template-columns:255px 255px 255px 255px;grid-template-areas:"card-1 card-1 card-2 card-5" "card-3 card-4 card-4 ."}}@media (max-width: 1200px) and (min-width: 377px){.Container .Row{width:95%;grid-template-rows:auto;grid-template-columns:auto auto auto;grid-template-areas:"card-1 card-1 card-2" "card-3 card-4 card-4" "card-5 card-5 card-5"}}

